rection:column;align-items:center;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;border-radius:6px;margin:2%}.card a{display:flex;flex-direction:column;align-items:center}.card a .product-img{width:70%;min-width:195px;height:200px;margin-bottom:25px;border-radius:5px;object-fit:contain}.card a h2{font-size:1em;padding-top:15px;padding-bottom:15px;height:68px}.card a button{padding:10px;border-radius:5px;background-color:#691727;color:#fff;border:1px solid #691727;transition-duration:0.6s}.card a button:hover{border:1px solid #691727;color:#691727;background-color:#fff}.card .price{font-size:1em;padding-top:15px;padding-bottom:15px}.item-detail{display:flex;flex-direction:column;padding:5%;width:100%}.item-detail .detail{display:flex}.item-detail .detail img{width:50%;padding:5%;border-radius:5px;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px}.item-detail .detail .item-detail-text{display:flex;flex-direction:column}.item-detail .detail .item-detail-text h1{font-size:1.5em;padding:5%}.item-detail .detail .item-detail-text p{font-size:1em;padding:5%}.item-detail .detail .item-detail-text p:nth-of-type(2){font-size:1.5em;font-weight:bold;align-self:flex-end}.item-detail .item-counter{width:25%;margin-top:25px;align-self:flex-end}.item-detail .buy-button{width:25%;align-self:flex-end}.item-detail .buy-button a{color:#fff}.footer{display:flex;width:100%;padding:5%;background-color:#fafafaa4}.footer .footer-col-1{padding:3%}.footer .footer-col-1 h2{font-size:1em;font-weight:normal}.footer .footer-col-2{padding:3%;align-self:center}.footer .footer-col-2 h3{font-size:1em;font-weight:normal}.footer .footer-col-2 h3:first-child{font-weight:bold}.footer .footer-col-3{padding:3%;align-self:center}.footer img{width:55%;padding-bottom:2%}.cart-item{width:100%;display:flex;justify-content:space-between;padding-top:2%}.cart-item .cart-img{width:25%}.cart-item .cart-img img{width:50%}.cart-item h2{font-size:16px;font-weight:normal;width:33%;text-align:center}.cart-item button{width:25px;height:25px}.cart{display:flex;flex-direction:column;padding:2%;text-align:center}.cart .cart-title{font-weight:bold;display:flex;justify-content:space-between;width:100%;padding-top:2%}.cart .cart-title h2{width:25%}.cart .no-products{padding-top:5%}.cart .total{width:25%;align-self:flex-end;padding-top:2%}.cart button{padding:10px;border-radius:5px;background-color:#691727;color:#fff;border:1px solid #691727;transition-duration:0.6s;width:25%;align-self:flex-end;margin-top:2%}.cart button:hover{border:1px solid #691727;color:#691727;background-color:#fff}.cart .empty-btn{width:15%}form{border-radius:5px;background-color:#f2f2f2;padding:20px;margin-top:2%;width:35%;align-self:center}form input{width:100%;padding:12px 20px;margin:8px 0;display:inline-block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}.order{display:flex;justify-content:center;align-items:center;flex-direction:column}.order .order-title{text-align:center;font-size:36px;width:100%;padding:2%}.order .order-subtitle{font-weight:bold;display:flex;justify-content:space-between;width:100%;padding-left:10%}.order .order-subtitle h2{width:25%}.order .order-detail{padding-left:10%;padding-top:5%}.order .order-detail button{display:none}.order .total{width:25%;align-self:flex-end;padding:2%}*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;font-family:'Montserrat', sans-serif;color:#000}

